.error-container {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.error-container .error-card {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 2rem;
  background: #364f6b;
  width: 80%;
  height: 80%;
}

.error-container .error-card img {
  width: 20rem;
}
.error-container .error-card button {
  background: #feb062;
  color: white;
  border-radius: 0.5rem;
  border: none;
  font-size: 1rem;
  padding: 1rem;
}
.error-container .error-card h1 {
  font-size: 6rem;
}
.error-container .error-card h1,
.error-container .error-card h2 {
  color: #feb062;
}
